<template>
  <el-row type="flex" justify="center">
    <el-col :lg="8" class="center">
      <h1 class="title">登录蓝旭博客</h1>
      <el-form  class="form">
        <el-form-item>
          <el-input @keyup.enter="req" v-model="user.username" type="text" size="large" placeholder="用户名">
            <template #prepend><el-icon><User/></el-icon></template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input @keyup.enter="req" v-model="user.password" type="password" show-password size="large" placeholder="密码">
            <template #prepend><el-icon><Lock/></el-icon></template>
          </el-input>
        </el-form-item>
        <el-row type="flex" justify="center">
          <el-button type="primary" round plain @click="error">登录</el-button>
          <el-button type="primary" round plain @click="error">注册</el-button>
        </el-row>
        <el-row type="flex" justify="end">
          <el-dropdown trigger="click">
            <el-button>更多</el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item>
                  <router-link to="/index">
                    <el-link :underline="false">返回首页</el-link>
                  </router-link>
                </el-dropdown-item>
                <el-dropdown-item>
                    <el-link @click="error" :underline="false">忘记密码</el-link>
                </el-dropdown-item>
                <el-dropdown-item>
                  <router-link to="/user">
                    <el-link @click="login" :underline="false">游客登录</el-link>
                  </router-link>
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </el-row>
      </el-form>
    </el-col>
  </el-row>
</template>

<script setup>
import {getCurrentInstance, onMounted, onUnmounted, ref} from "vue"
import {Lock, User} from "@element-plus/icons-vue"
const {proxy} = getCurrentInstance()

const user = ref({
  username: '',
  password: '',
})

import backImg from '@/assets/image/1331048.png'
import {ElNotification} from "element-plus";

onMounted(() => {
  // const body = document.body.style;
  // body.backgroundImage = "url(" + backImg + ")";
  // body.width = "100%";
  // body.height = "100%";
  // body.backgroundAttachment = "fixed";
  // body.backgroundSize = "cover";
  // body.backgroundPosition = "center";
})
onUnmounted(() => {
  // document.body.style = "";
})

const error = ()=> {
  ElNotification({
    title: 'warning',
    message: '暂未开放接口',
    type: 'warning'
  })
}

function login(){
  const token = 'visitor'
  window.localStorage.setItem('Authorization', token)
}

function req(){
  proxy.$axios.post('/test',{
    username: user.value.username,
    password: user.value.password
  },{
    headers: {
      'Authorization' : window.localStorage.getItem('Authorization'),
      'Content-Type' : 'application/json',
    }
  })
      .then(response => {
        let token = window.localStorage.getItem('Authorization')
        if(token == null) window.localStorage.setItem('Authorization',response.headers.get('Authorization'))
      })
      .catch(error => console.log(error))
}
</script>

<style scoped>
.center{
  padding-top: 50px;
  margin: 150px;
  border-radius: 15px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.2);
  opacity: 1;
}
.form{
  padding: 40px 60px 60px 60px;
}
.title{
  font-family:  "方正楷体", "楷体" , "SimSun-ExtB";
  background-image: linear-gradient(-135deg, #418ae0, #56a0d8, #dc8bc3, #56a0d8, #418ae0, #56a0d8, #dc8bc3, #56a0d8, #418ae0);
  -webkit-text-fill-color: rgba(0,0,0,0);
  -webkit-background-clip: text;
  background-size: 200% 100%;
  -webkit-animation: masked-animation 2.5s infinite linear;
  animation-duration: 2.5s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
}
@keyframes masked-animation {
  0% {
    background-position: 100% 0;
  }
  100% {
    background-position: 0 100%;
  }
}
</style>
